body {
  background-color: var(--primary-bg);
  color: var(--primary-text);
  height: 100%;
  font-family: "Inter", sans-serif;
  font-weight: 400;
}

.text--underline {
  text-decoration: underline;
}
.text--emphasis {
  font-style: italic;
  font-weight: 400;
}
.text--center {
  text-align: center;
}

a, a:hover, a:visited, a:link {
  color: #00a27c;
}

.text--primary {
  color: #00a27c;
}

.bg--primary {
  background-color: #00a27c;
  color: white;
}

.text--yellow {
  color: #f4b30c;
}

.bg--yellow {
  background-color: #f4b30c;
  color: white;
}

.text--red {
  color: #cd1919;
}

.bg--red {
  background-color: #cd1919;
  color: white;
}

.text--green {
  color: #08cf9f;
}

.bg--green {
  background-color: #08cf9f;
  color: white;
}

.text--purple {
  color: #5c72b5;
}

.bg--purple {
  background-color: #5c72b5;
  color: white;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
}
@media (min-width: 576px) {
  .container {
    max-width: 576px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 992px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}
.container--row {
  display: flex;
  flex-direction: row;
  flex-grow: 0;
  align-items: start;
  justify-content: space-between;
}
@media (max-width: 992px) {
  .container--row {
    flex-direction: column;
  }
}
.container--column {
  display: flex;
  flex-direction: column;
  flex-grow: 0;
  align-items: start;
  justify-content: space-between;
}

.button {
  display: inline-block;
  text-decoration: none;
  border-radius: 3.2rem;
  border-width: 2px;
  border-style: solid;
  padding-left: 2.2rem;
  padding-right: 2.2rem;
  padding-top: 0.8rem;
  padding-bottom: 0.8rem;
  font-weight: 700;
  font-size: 1.2rem;
}
.button--primary {
  background-color: var(--inverse-bg) !important;
  color: var(--inverse-text) !important;
}
.button--secondary {
  background-color: var(--primary-bg) !important;
  color: var(--primary-text) !important;
  border-color: var(--inverse-bg) !important;
}

.tags {
  display: flex;
  flex-direction: row;
  flex-grow: 0;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: start;
  align-content: start;
}
.tags .tag {
  display: inline-block;
  border-radius: 6rem;
  padding-left: 0.8rem;
  padding-right: 0.8rem;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
  margin-right: 0.4rem;
  margin-top: 0.4rem;
  font-size: 0.8rem;
  font-weight: 500;
  flex-shrink: 0;
}

.header {
  width: 100%;
  max-height: 7.8rem;
  height: 7.8rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.header__logo {
  max-height: 7.8rem;
  height: 7.8rem;
  width: auto;
  flex-shrink: 0;
}
.header__social {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  flex-grow: 0;
  justify-content: space-evenly;
}
.header__social-link {
  font-size: 1.6rem;
  padding: 3rem;
}
.header__social-link, .header__social-link:visited, .header__social-link:active, .header__social-link:hover {
  color: var(--primary-text);
}
.header__nav {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-evenly;
  margin-left: auto;
}
.header__nav-item {
  display: block;
  border-radius: 1000rem;
  text-decoration: none;
  padding-left: 1.6rem;
  padding-right: 1.6rem;
  padding-top: 0.4rem;
  padding-bottom: 0.4rem;
  font-size: 1rem;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  margin: 0 1rem;
}
.header__nav-item, .header__nav-item:visited, .header__nav-item:active, .header__nav-item:hover {
  color: var(--primary-text);
}
.header__nav-item--active {
  background: #00a27c;
  font-weight: 700;
}
@media (max-width: 992px) {
  .header__nav {
    display: none;
    width: 100%;
    margin-top: 1rem;
    background-color: var(--secondary-bg);
    border: 2px solid var(--primary-bg);
    flex-direction: column;
  }
  .header__nav-item {
    width: 100%;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 0.4rem;
    text-align: center;
    font-size: 1.2rem;
  }
}
@media (max-width: 992px) {
  .header {
    flex-wrap: wrap;
    max-height: unset;
    height: unset;
    overflow: visible;
  }
}

.hero {
  margin-top: 0 !important;
}
.hero__text {
  max-width: 50%;
  margin-top: 3rem;
  margin-bottom: 3rem;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2rem;
  order: 1;
}
@media (max-width: 768px) {
  .hero__text {
    max-width: 100%;
    margin-bottom: 1rem;
  }
}
.hero__text-title {
  font-size: 3.8rem;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 1rem;
  line-height: 3.8rem;
}
@media (max-width: 768px) {
  .hero__text-title {
    margin-top: 1rem;
  }
}
@media (max-width: 992px) {
  .hero__text {
    max-width: 100%;
    width: 100%;
    order: 2;
  }
}
.hero__text-controls {
  display: flex;
  width: 100%;
  flex-direction: row;
  flex-shrink: 0;
  flex-grow: 0;
  justify-content: start;
}
.hero__text-controls .button:not(:last-child) {
  margin-right: 1.5rem;
}
.hero__image {
  max-width: 50%;
  width: 50%;
  padding: 1.5rem;
  margin-bottom: -7rem;
  overflow: visible;
  order: 2;
}
@media (max-width: 576px) {
  .hero__image {
    display: none;
  }
}
@media (max-width: 992px) {
  .hero__image {
    max-width: 100%;
    width: 100%;
    order: 1;
  }
}
@media (max-width: 576px) {
  .hero {
    padding: 1rem;
  }
}

.section {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.section--alt {
  background-color: var(--secondary-bg);
}
.section__title {
  font-size: 3.4rem;
  font-weight: 700;
  margin-bottom: 1rem;
  margin-right: 1rem;
  line-height: 3.8rem;
}
@media (max-width: 768px) {
  .section__title {
    margin-top: 1rem;
  }
}
@media (max-width: 992px) {
  .section__title {
    margin-right: 0;
  }
}
.section__text {
  margin-top: 1rem;
  margin-left: 1rem;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2rem;
  width: 100%;
}
@media (max-width: 992px) {
  .section__text {
    margin-left: 0;
  }
}
@media (max-width: 768px) {
  .section__text {
    max-width: 100%;
    margin-bottom: 1rem;
  }
}
.section__text--half {
  max-width: 50%;
}
@media (max-width: 992px) {
  .section__text--half {
    width: 100%;
    max-width: 100%;
  }
}
.section__heading {
  font-size: 2.4rem;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 2rem;
  line-height: 3rem;
}
.section__heading-icon {
  margin-right: 0.4rem;
}

.packages {
  margin-top: 1rem;
  margin-left: 1rem;
}
@media (max-width: 992px) {
  .packages {
    margin-left: 0;
  }
}
.packages .package {
  background-color: var(--inverse-bg);
  color: var(--inverse-text);
  border-radius: 0.8rem;
  display: flex;
  text-decoration: none;
  flex-direction: row;
  flex-grow: 0;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.packages .package + .package {
  margin-top: 1rem;
}
.packages .package__name {
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  width: 25%;
  flex-shrink: 0;
  margin-right: 2rem;
}
@media (max-width: 768px) {
  .packages .package__name {
    text-align: left;
    width: unset;
  }
}
@media (max-width: 768px) {
  .packages .package {
    flex-direction: column;
    align-items: start;
    padding: 2rem;
  }
}